




















table.shipping-zone-table tbody tr td {
  padding: 15px;
}
table.shipping-zone-table tbody tr td:first-child {
  padding-left: 15px;
}
table.shipping-zone-table tbody tr td .row-actions {
  visibility: hidden;
  font-size: 12px;
  color: #ccc;
  clear: both;
  padding-top: 8px;
}
table.shipping-zone-table tbody tr td .row-actions .delete a {
  color: #A05;
}
table.shipping-zone-table tbody tr td .row-actions .delete a:hover {
  color: red;
}
table.shipping-zone-table tbody tr td span.is-disabled {
  text-decoration: line-through;
}
table.shipping-zone-table tbody tr:hover .row-actions {
  visibility: visible;
}

